.header {
    color: #18181A;
    background: #FFFFFF;
    right: 0;
    left: 0;
    top: 0;
    z-index: 10;
    -webkit-box-shadow: 0px 4px 20px rgba(60, 69, 108, 0.05);
    box-shadow: 0px 4px 20px rgba(60, 69, 108, 0.05);
}

@media (max-width: 1200px) {
    .header {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        z-index: 5;
        width: 100%;
    }
}
