* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Arial", sans-serif;
  color: #5F5F67;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 500;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}
