@media (max-width: $mobile-width-only) {

    body.nav--opened {
        overflow-y: hidden;
    }
    
    .nav {
        opacity: 0;
        height: 0;
        transition: all 0.2s ease-out;
        pointer-events: none;
    }

    body.nav--opened .nav { 
        opacity: 1;
        display: flex;
        position: absolute;
        top: 95px;
        left: 0;
        height: calc(100vh - 71px);
        width: 100%;
        padding: 0;
        padding-top: 30px;
        padding-bottom: 120px;
        overflow: hidden;
        overflow-y: auto;
        box-sizing: border-box;
        flex-direction: column;
        justify-content: flex-start;
        z-index: 1000;
        transition: all 0.2s ease-out;
        background-color: #FBFBFC;        
        pointer-events: auto;
    }

    .nav__wrap {
        width: 100%;
    }

    .nav__list {
        padding: 0;
        margin: 0;
        margin-bottom: 40px;
        list-style: none;
    }

    .nav__item {
        border-bottom: 1px solid #E7ECF3;
    }
    

    .nav__link {
        display: block;
        padding-top: 19px;
        padding-bottom: 19px;
        padding-left: 20px;
        cursor: pointer;
        color: #18181A;
        font-weight: bold;

        &:hover {
            color: #3F5199;
        }

        &:active {
            color: #3F5199;
        }
    }

    .nav__link--active {
        display: none;       
    }

    .nav__block-title {
        padding-left: 20px;
        font-weight: bold;
    }

    .nav__accordeon-container--closed .nav__block-title {
        &::before {       
            transform: translateY(50%) rotate(180deg);
            transition: .2s ease-in;
            top: 6px;
        }
    }    

    .nav__accordeon-container {
        position: relative;
        padding-top: 22px;
    }

    .nav__accordeon-container--closed .nav__accordeon-container {
        padding-bottom: 22px;
    }

    .nav__btn {
        display: block;
        background-color: transparent;
        border: 0;
        outline: 0;
        position: absolute;
        width: 100%;
        height: 66px;
        top: 0;
        left: 0;
        border: none;
    }    
    
    .nav__submenu {
        margin: 0;
        padding: 20px 0;
        margin-top: 20px;
        list-style: none;
        pointer-events: auto;
        background-color: #ffffff;
        border-top: 1px solid #E7ECF3;
        border-left: 1px solid #E7ECF3;
        border-right: 1px solid #E7ECF3;
    }

    .nav__accordeon-container--closed .nav__submenu {
        padding: 0;
        margin: 0;
        margin-left: 40px;
        list-style: none;
        max-height: 0;     
        opacity: 0;       
        will-change: max-height;
        //display: none;           
        transition: all 0.5s ease-out;        
        pointer-events: none;
    }    

    .nav__submenu-link {
        display: block;
        padding-top: 5px;
        padding-bottom: 5px;
        padding-left: 20px;
        cursor: pointer;
        font-size: 15px;
        color: #575757;

        &:hover {
            color: #E53935;
        }

        &:active {
            color: #E53935;
        }
    }     
    

    .nav__contacts-wrap {
        display: block;
    }

    .nav__contacts {
        position: relative;
        padding-top: 20px;
        padding-left: 54px;
        padding-right: 20px;
        padding-bottom: 20px;
        margin-bottom: 30px;
        border: 1px solid #EFEFEF;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 20px;    
            transform: translateY(-50%);        
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            
        }
    }    

    .nav__contacts--address {
        &::before {
            background-image: url("../img/icon-map.svg");
        }        
    }

    .nav__contacts--email {
        &::before {
            background-image: url("../img/icon-email.svg");
        }        
    }

    .nav__contacts--work {
        &::before {
            background-image: url("../img/icon-work.svg");
        }        
    }
    
    
    /* Скрываем scrollbar для Chrome, Safari и Opera */
    body.nav--opened .nav::-webkit-scrollbar {
        display: none;
    }

    /* Скрываем scrollbar для IE, Edge и Firefox */
    body.nav--opened .nav {
        -ms-overflow-style: none;  /* IE и Edge */
        scrollbar-width: none;  /* Firefox */
    }

}    

@media (min-width: 576px) {

    .nav {
        top: 133px;
    }

    .nav__contacts-wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }

    .nav__address {
        grid-column: 1/3;
        margin-bottom: 0;
    }

    .nav__contacts {
        grid-column: 1/2;
        margin-bottom: 0;

        &::before {
            top: 18px;
        }
    }

    .nav__email {
        grid-column: 2/3;

        &::before {
            top: 18px;
            left: 18px;
        }
    }

}    

@media (min-width: 768px) {  
    .nav {
        top: 144px;
    }

    .nav__list {
        margin-bottom: 50px;
    }

    .nav__contacts-wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 0;
    }

    .nav__address {
        width: 31%;
        
    }

    .nav__contacts {
        width: 31%;
    }

    .nav__email {
        width: 31%;
    }
}    

@media (min-width: 992px) {
    .nav {
        top: 150px;
    }

    .nav__accordeon-container {
        padding-top: 20px;
    }

    .nav__list {
        margin-bottom: 60px;
    }

    .nav__contacts {
        display: none;
    }

    .nav__address {
        padding-top: 24px;
        padding-bottom: 24px;
        padding-left: 74px;
        width: 48%;

        &::before {
            top: 23px;
            left: 30px;
        }
        
    }

    .nav__email {
        padding-top: 24px;
        padding-bottom: 24px;
        padding-left: 74px;
        width: 48%;

        &::before {
            top: 26px;
            left: 30px;
        }
    }
}    



@media (min-width: $desktop-width) {
    .nav {
        position: static;
        top: 0;
        height: auto;
        border-top: 1px solid #E2E4F0;
    }

    .nav__block-title {
        color: #18181A;
        font-weight: bold;

        &:hover {
            color: #3F5199; 
        }

        &:active {
            color: #3F5199; 
        }
    }

    .nav__btn {
        display: none;
    }
    
    .nav__list {
        position: relative;
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;  
    }    

    .nav__item {
        padding-left: 10px;
        padding-top: 25px;
        padding-bottom: 25px;
        
        &:first-child {
            padding-left: 0;
        }
    }
    
    .nav__link {        
        color: #18181A;
        font-weight: bold;

        &:hover {
            color: #3F5199; 
        }

        &:active {
            color: #3F5199; 
        }
    }

    .nav__link--active {
        display: block;
        position: relative;   
    }       

    .nav__contacts-wrap {
        display: none;
    }

    .nav__submenu {
        opacity: 0;
        margin: 0;
        padding-top: 40px;
        padding-left: 40px;
        padding-right: 30px;
        list-style: none;
        position: absolute;
        top: 70px;
        left: 0;
        width: 0;
        overflow: hidden;
        max-height: 0;
        z-index: 1000;
        box-shadow: 0 20px 30px -12px rgba(34,60,80,.1);
        border-radius: 16px;
        background-color: #fff;
        transition: .2s ease-in;
        border: 1px solid #efefef;
        column-count: 2;
        pointer-events: none;
        
    }

    .nav__item--section {
        position: static;
        cursor: pointer;       
        padding-right: 20px;        
    
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            right: 1px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 10px;
            height: 6px;
            background-image: url("../img/icon-open.svg");
            transition: .2s ease-in;
        }
        ////////////////////ПРАВКИ ТУТ!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!        

        
    }

    .nav__item--section:hover {
        .nav__submenu {   
            padding-top: 40px;            
            opacity: 1;      
            width: 816px;           
            max-height: 700px; 
            transition: .2s ease-in;
            padding-bottom: 40px;
            pointer-events: auto;
        }

        .nav__block-title::before {
            top: 8px;
            transform: rotate(0deg);
        }
    }    

    .nav__accordeon-container {
        padding-top: 0;
    }

    

    .nav__submenu-item {
        margin-bottom: 8px;
    }

    .nav__socials.socials {
        display: none;
    }
    
}  

.nav__block-title {
    position: relative;
    display: inline-block;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        right: -15px;
        width: 7px;
        height: 5px;
        transform: translateY(-50%);
        background-image: url("../img/icon-arrow.svg");
        background-size: contain;
        background-repeat: no-repeat;
        transition: .2s ease-in;
    }
}

.nav__accordeon-container--closed .nav__block-title {
    &::before {       
        transform: translateY(50%) rotate(180deg);
        transition: .2s ease-in;
        top: 6px;
    }
}    