.socials {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.socials__item {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 15px;

    &:last-child {
        margin-right: 0;
    }
}

.socials__link {
    text-decoration: none;
    color: inherit;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s linear;

    &::before {
        content: "";
        display: block;
        background-repeat: no-repeat;
        background-size: contain;
        z-index: 150;
        width: 20px;
        height: 20px;
    }
}

.socials__link--telegram {
    background-color: #039BE5;

    &::before {
        background-image: url("../img/icon-tel.svg");
    }

    &:hover {
        background-color: #0488C8;
    }

    &:active {
        background-color: #0488C8;
    }
}

.socials__link--whatsapp {
    background-color: #43D854;

    &::before {
        background-image: url("../img/icon-whatsapp.svg");
    }

    &:hover {
        background-color: #36B544;
    }

    &:active {
        background-color: #36B544;
    }
}

.socials__link--vk {
    background-color: #1976D2;

    &::before {
        background-image: url("../img/icon-vk.svg");
    }

    &:hover {
        background-color: #1862AB;
    }

    &:active {
        background-color: #1862AB;
    }
}

.socials__link--inst {
    background: linear-gradient(43.94deg, #EF9033 16.16%, #F13459 50.11%, #E329BC 83.68%);

    &::before {
        width: 20px;
        height: 20px;
        background-image: url("../img/icon-inst.svg");
    }

    &:hover {
        background: linear-gradient(43.94deg, #CD7825 16.16%, #C72847 50.11%, #C824A6 83.68%);


    }

    &:active {
        background: linear-gradient(43.94deg, #CD7825 16.16%, #C72847 50.11%, #C824A6 83.68%);
    }
}



