.main-navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
}

.main-navigation__logo {
    padding-top: 2px;
}

.main-navigation__address {
    display: none;
}

.main-navigation__callback {
    display: none;
}

.main-navigation__toggle {
    display: none;
}

.main-navigation__socials {      
    display: none;
}

.main-navigation__email {
    display: none;
}

.main-navigation__callback-tel {
    order: -1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 48px;
    height: 48px;
    background-color: #3F5199;
    border-radius: 8px;
    cursor: pointer;
    font-size: 0;
    transition: all 0.3s linear;

    &::before {
        content: "";
        display: block;
        background-repeat: no-repeat;
        background-size: contain;            
        width: 24px;
        height: 24px;
        background-image: url("../img/icon-phone-white.svg");
    }

    &:hover {
        background-color: #5369C0;
    }

    &:active {
        background-color: #5369C0;
    }
}

@media (min-width: 576px) {        
}

@media (min-width: 768px) {

    .main-navigation__socials {
        display: block;
    }
}

@media (min-width: 992px) { 

    .main-navigation__callback-tel {
        margin-left: 20px;
    }
    
    .main-navigation__callback {
        display: block;
        text-align: right;
        padding-bottom: 7px;
        margin-left: 30px;
    }

    .main-navigation__tel {
        display: block;
        position: relative;
        font-size: 22px;
        color: #000;
        font-weight: bold;

        &:hover {
            color: #3F5199;
        }
    
        &:active {
            color: #3F5199;
        }

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: -28px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-phone.svg");
        }
    }

    .main-navigation__work {
        display: inline-block;
        position: relative;
        padding: 0;
        margin: 0;
        font-size: 14px;
        color: #3F5199;
    }
}    

@media (min-width: $desktop-width) {
    .main-navigation {
        padding-top: 30px;
        padding-bottom: 30px;
    }

    .main-navigation__address {
        position: relative;
        display: block;
        max-width: 215px;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: -30px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-map.svg");
        }
    }

    .main-navigation__socials {
        align-items: flex-start;
        padding-bottom: 5px;
    }       

    .main-navigation__callback {
        margin-left: 0;
        padding-bottom: 0;
        padding-right: 10px;
        padding-left: 30px;
        padding-bottom: 5px;
    }

    .main-navigation__callback-tel {
        display: none;
    }
    

    .main-navigation__email {
        display: block;
        color: #3F5199;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: -30px;
            transform: translate(0, -50%);
            background-repeat: no-repeat;
            background-size: contain;
            width: 24px;
            height: 24px;
            background-image: url("../img/icon-email.svg");
        }
    }

    .main-navigation__link-email {
        display: block;
        color: #000000;

        &:hover {
            color: #3F5199;
        }

        &:active {
            color: #3F5199;
        }
    }
}

@media (max-width: $mobile-width-only) {
    .main-navigation__toggle {
        width: 48px;
        height: 48px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #3F5199;
        transform: translateX(0);
        transition: transform 0.3s ease-in;
        color: #ffffff;
        border-radius: 8px;
        border: none;

        &:hover {
            background-color: #5369C0;
        }

        &:active {
            background-color: #5369C0;
        }
      }

      .main-navigation__toggle-inner {
        width: 18px;
        height: 17px;
      }
    
      /*.main-navigation__toggle:focus {
        outline: 0;
        color: #E53935;
      }*/
    
      .main-navigation__toggle-bar,
      .main-navigation__toggle-inner::after,
      .main-navigation__toggle-inner::before {
        content: "";
        display: block;
        width: 18px;
        height: 2px;
        margin-bottom: 4px;
        background-color: currentColor;
        transition: transform 0.3s ease;
      }
    
      .main-navigation__toggle-bar,
      .main-navigation__toggle-inner::after {
        margin-bottom: 0;
      }
    
      .main-navigation__toggle-bar {
        margin-bottom: 4px;
      }
    
      .main-navigation__bottom {
        padding-top: 0;
      }
    
      .main-navigation__toggle--active .main-navigation__toggle-bar {
        opacity: 0;
      }
    
      .main-navigation__toggle--active .main-navigation__toggle-inner::before {
        transform: translateY(6px) rotateZ(135deg);
      }
    
      .main-navigation__toggle--active .main-navigation__toggle-inner::after {
        transform: translateY(-6px) rotateZ(-135deg);
      }
    
}    


